#pre-game {
  width: fit-content;
  margin: 10% auto;
  padding: 5%;
  display: flex;
  flex-direction: column;
  background-color: slategrey;
  color: white;
}
.start-btn {
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.bubble-mat {
  background-color: lightblue;
  height: 95vh;
  overflow: hidden;
}

#scoreboard {
  display: flex;

  height: 5vh;
  padding: 0 15%;
  background-color: slategrey;
  color: white;

  align-items: center;
  font-family: monospace;
  font-size: 2em;
}

.bubble {
  /* border: 1px solid red; */
  border-radius: 85px;
  margin: 0.5%;
}

@media only screen and (min-width: 1520px) and (max-width: 2000px) {
  .bubble {
    width: 190px;
  }
}

@media only screen and (min-width: 1000px) and (max-width: 1520px) {
  .bubble {
    width: 150px;
  }
}

@media only screen and (min-width: 0px) and (max-width: 500px) {
  .bubble {
    width: 100px;
  }
}

#timer {
  position: fixed;
  font-family: monospace;
  font-size: 2.5em;
}
